// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 
import Vue from 'vue'
import App from './App'
import router from './router'

import Vuex from 'vuex'
import store from './vuex/store'
Vue.use(Vuex);

//js
// var myFilter = require('./assets/js/filter.js')
import myFilter from './assets/js/filter.js'
//
Vue.prototype.myFilter = myFilter;

import '../static/mock/mock.js';
import 'flex.css';

import axios from 'axios';
Vue.prototype.$ajax = axios;

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App },
  watch:{
    '$route':function(to,from){
        // console.log("")
    }
  }
});
router.beforeEach((to, from, next) => {
  var auth = to.meta.auth;//
  var isLogin = Boolean(store.state.user.username); //true false
  if (auth && !isLogin && to.path !== '/login') {
    return next({
      path: '/login',
      query: {redirect: to.fullPath}  // path
    })
  }
  next()
});




