import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

/* Mutation */
export const USER_LOGIN = 'USER_LOGIN';
export const USER_LOGOUT = 'USER_LOGOUT';

const store = new Vuex.Store({
  // 
  state: {
    author: 'A.Q.I',//
    listData : '',//index
    listAjax : true,//ajax
    posState : '',//
    user: JSON.parse(sessionStorage.getItem('user')) || {},//
    commitList : []//
  },
  /*mutationsstatestate*/
  mutations: {
    /**/
    SAVE_POSITION (state, position) {
      state.posState = position
    },
    /**/
    SAVE_LISTDATA(state ,listData){
      state.listData = listData
    },
    SAVE_COMMIT(state,commitList ){
      state.commitList.unshift(commitList)
    },
    /**/
    //  ES2015 
    [USER_LOGIN](state,user){
      sessionStorage.setItem('user',JSON.stringify(user));
      /*userstate*/
      Object.assign(state.user, user)
    },
    /**/
    [USER_LOGOUT](state){
      sessionStorage.removeItem('user');
      /*user*/
      Object.keys(state.user).forEach(k => Vue.delete(state.user, k))
    },
  },
  /*actionsmutation,*/
  actions:{
    /**/
    [USER_LOGIN]({commit}, user){
      commit(USER_LOGIN, user)
    },
    /**/
    [USER_LOGOUT]({commit}){
      commit(USER_LOGIN)
    }
  }

});

export default store



