<template>
  <!---->
  <div id="mescroll" class="mescroll">
    <!--,QQ,SafariiOS,<div class="mescroll-bounce"><div>,2,dom-->
    <div class="mescroll-bounce">
      <!---->
      <ul id="dataList" class="data-list">
        <li v-for="pd in pdlist" flex>
          <img :src="pd.dataImage" alt="">
           <div class="dataList_right">
             <p>{{pd.longText}}</p>
             <span>{{pd.province+pd.email}}</span>
           </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script >
  import 'mescroll.js/mescroll.min.css'
  import MeScroll from 'mescroll.js'
export default{
  name : "scroll",
  data(){
    return {
      mescroll: null,
      pdlist: []
    }
  },
  mounted() {
    //MeScroll,down,,
    //: mescroll.resetUpScroll(); resetUpScrollpage.num=1,up.callback,;
    var self = this;
    self.mescroll = new MeScroll("mescroll", {
      up: {
        callback: self.upCallback, //,mescroll
        //,
        //page:{size:8}, //8,10
        toTop:{ //
          src : "../res/img/mescroll-totop.png", //1000px,offset
          //offset : 1000
        },
        empty:{ //
          warpId:"dataList",
          icon : "../res/img/mescroll-empty.png" ,
//						  	tip : ",~" ,
//						  	btntext : " >" ,
//						  	btnClick : function() {
//						  		alert("");
//						  	}
        }
      }
    });

    //vue,vue
    document.getElementById("dataList").style.display="block";
  },
  methods: {
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback(page) {
      //
      var self = this;
      console.log(self)
      getListDataFromNet(page.num, page.size, function(curPageData) {
//        curPageData=[]; //,empty

        //
        if(page.num == 1) self.pdlist = [];

        //
        self.pdlist = self.pdlist.concat(curPageData);
        console.log(self.pdlist)
        //,;
        //mescroll,,;,;
        console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.length+", self.pdlist.length==" + self.pdlist.length);

        //():  totalPage
        //self.mescroll.endByPage(curPageData.length, totalPage); //(, )

        //():  totalSize
//        self.mescroll.endBySize(curPageData.length, 20); //(, )

        //():  hasNext
        self.mescroll.endSuccess(curPageData.length, true); //(, true/false)

        // (),:20,10,2.,,hasNext,.
//        self.mescroll.endSuccess(curPageData.length);

      }, function() {
        //,;
        self.mescroll.endErr();
      });
      function getListDataFromNet(pageNum,pageSize,successCallback,errorCallback) {
        //,
//    setTimeout(function () {
        self.$ajax.get("http://mockjs", {
//          params: {
//            num: pageNum, //
//            size: pageSize //
//          }
          })
          .then(function(response) {
            var data=response.data.result; // : ../res/pdlist1.js
            var listData=[];//
            for (var i = (pageNum-1)*pageSize; i < pageNum*pageSize; i++) {
              if(i==data.length) break;
              listData.push(data[i]);
            }
            successCallback&&successCallback(listData);//
          })
          .catch(function(error) {
            errorCallback&&errorCallback();//
          });
//    },500)
      }

    },

  }

}
  /**/
//PC,;,
//document.ondragstart=function() {return false;}
</script>

<style scoped lang="less">
  .mescroll{
    background: #fff;
  }
  ul>li{
    padding: .5rem .5rem;
    /*display: flex;*/
    /*align-items: flex-start;*/
    border-bottom: 1px solid #e4e4e4;
  }
  li>img{
    margin-right: .2rem;
    width: 20%;
    height: 20%;
  }
  .dataList_right{
    /*flex: 1;*/
    p{
      margin-bottom: .3rem;
    }
  }
</style>



